define('fusor-ember-cli/components/textarea-f', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({

    rowsPassed: (function () {
      if (this.get('rows')) {
        if (this.get('rows') > 0) {
          return true;
        }
      }
    }).property('rows'),

    numRows: (function () {
      return this.getWithDefault('rows', '');
    }).property(),

    numCols: (function () {
      return this.getWithDefault('cols', '');
    }).property()

  });

});